import {
  getSolidDataset,
  getThing,
  setThing,
  getStringNoLocale,
  setStringNoLocale,
  saveSolidDatasetAt
} from "@inrupt/solid-client";
import { Session } from "@inrupt/solid-client-authn-browser";
import { VCARD } from "@inrupt/vocab-common-rdf";

const SOLID_IDENTITY_PROVIDER = "https://solidcommunity.net";
document.getElementById(
  "solid_identity_provider"
).innerHTML = `[<a target="_blank" href="${SOLID_IDENTITY_PROVIDER}">${SOLID_IDENTITY_PROVIDER}</a>]`;

const session = new Session();

const buttonLogin = document.getElementById("btnLogin");
const writeForm = document.getElementById("writeForm");

async function login() {
  if (!session.info.isLoggedIn) {
    await session.login({
      oidcIssuer: SOLID_IDENTITY_PROVIDER,
      clientName: "Inrupt tutorial client app",
      redirectUrl: window.location.href
    });
  }
}
let webID;
async function handleRedirectAfterLogin() {
  await session.handleIncomingRedirect(window.location.href);
  if (session.info.isLoggedIn) {
    // Use the webID declared inside this function
    webID = session.info.webId;
    fetchAndDisplayLocality(webID);
  }
}

let formattedLocality = "";


async function fetchAndDisplayLocality(webID) {
  try {
    new URL(webID);
  } catch (_) {
    document.getElementById("labelFN").textContent = `Provided WebID [${webID}] is not a valid URL - make sure you are logged in, with a SolidCommunity WebId`;
    return;
  }

  const profileDocumentUrl = new URL(webID);


  let myDataset;
  try {
    if (session.info.isLoggedIn) {
      myDataset = await getSolidDataset(profileDocumentUrl.href, { fetch: session.fetch });
    } else {
      myDataset = await getSolidDataset(profileDocumentUrl.href);
    }
  } catch (error) {
    document.getElementById("labelFN").textContent = `Entered value [${webID}] does not appear to be a WebID. Error: [${error}]`;
    return;
  }

  async function fetchIpsData(webID) {
    try {
      // Use the user's WebID to construct the URL for "ips.json"
      const ipsUrl = new URL(webID);
      ipsUrl.pathname = "ips.json";
  
      const response = await fetch(ipsUrl.href);
      if (response.ok) {
        const ipsData = await response.json();
        // Now you have access to the data from "ips.json" in the ipsData variable
        console.log(ipsData);
      } else {
        console.error('Failed to fetch ips.json');
      }
    } catch (error) {
      console.error('Error fetching ips.json:', error);
    }
  }

  if (session.info.isLoggedIn) {
    webID = session.info.webId;
    fetchIpsData(webID); // Fetch ips.json data
  }


  const profile = getThing(myDataset, webID);
  const formattedLocality = getStringNoLocale(profile, VCARD.locality);

  document.getElementById("labelFN").textContent = `${formattedLocality}`;
  document.getElementById("loginView").style.display = "none";
  document.getElementById("localityView").style.display = "block";
}

handleRedirectAfterLogin();

async function writeProfile(locality) {
  if (!session.info.isLoggedIn) {
    document.getElementById("labelFN").textContent = `First login before you can write [${locality}] to your SolidPod!`;
    document.getElementById("labelFN").setAttribute("role", "alert");
    return;
  }

  const webID = session.info.webId;
  const profileDocumentUrl = new URL(webID);
  profileDocumentUrl.hash = "";

  let myProfileDataset = await getSolidDataset(profileDocumentUrl.href, {
    fetch: session.fetch
  });

  let profile = getThing(myProfileDataset, webID);

  profile = setStringNoLocale(profile, "http://www.w3.org/2006/vcard/ns#locality", locality);

  myProfileDataset = setThing(myProfileDataset, profile);

  await saveSolidDatasetAt(profileDocumentUrl.href, myProfileDataset, {
    fetch: session.fetch
  });

  fetchAndDisplayLocality(webID);
}

buttonLogin.onclick = function () {
  login();
};

async function evaluateRule(webID) {
  let myDataset;
  try {
    if (session.info.isLoggedIn) {
      myDataset = await getSolidDataset(webID, { fetch: session.fetch });
    } else {
      myDataset = await getSolidDataset(webID);
    }

    const profile = getThing(myDataset, webID);
    const formattedLocality = getStringNoLocale(profile, VCARD.locality);

    const response = await fetch('openregel.json');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }

    const externalRule = await response.json();
    const { field, operator, value } = externalRule.condition;
    const ruleExpression = `"${formattedLocality}" ${operator} "${value}"`;
    const outcome = eval(ruleExpression);

    const ruleOutcome = document.getElementById('rule_outcome');
    ruleOutcome.textContent = `Rule Outcome: ${outcome}`;
  } catch (error) {
    console.error('Error:', error);
  }
}

const evalRuleButton = document.getElementById('eval_rule');
evalRuleButton.addEventListener('click', () => {
  const webID = session.info.webId;
  evaluateRule(webID);
});

const editButton = document.getElementById("editButton");
const saveButton = document.getElementById("saveButton");
const editForm = document.getElementById("editForm");
const inputName = document.getElementById("input_name");

editButton.addEventListener("click", async () => {
  let myDataset; // Declare myDataset here
  try {
    if (session.info.isLoggedIn) {
      myDataset = await getSolidDataset(webID, { fetch: session.fetch });
    } else {
      myDataset = await getSolidDataset(webID);
    }

    const profile = getThing(myDataset, webID);
    formattedLocality = getStringNoLocale(profile, VCARD.locality);
  } catch (error) {
    console.error('Error fetching the formatted locality:', error);
    return;
  }

  // Display the edit form and populate the input with the current locality
  editForm.style.display = "block";
  inputName.value = formattedLocality; // Set the input value to formattedLocality
  editButton.style.display = "none";
  labelFN.style.display = "none";
});

saveButton.addEventListener("click", async (event) => {
  event.preventDefault();
  await writeProfile(inputName.value);
  editForm.style.display = "none";
  editButton.style.display = "block";
  labelFN.style.display = "block";
  document.getElementById("labelFN").textContent = `${inputName.value}`;
});
